* {
  padding: 0;
  margin: 0;
  border: 0;
}
*::after, *::before {
  box-sizing: border-box;
}
*:focus, *:active {
  outline: none;
}
*[type=checkbox], *[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

html, body {
  width: 100%;
  height: 100%;
  line-height: 1;
  font-size: 100%;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  text-decoration: none;
}

img {
  vertical-align: top;
}

input, button, textarea {
  font-family: inherit;
}

button {
  cursor: pointer;
}

nav, footer, header {
  display: block;
}

ul, ol, li {
  list-style: none;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

@font-face {
  font-family: "PlusJakartaSans";
  src: url("../fonts/PlusJakartaSans.woff2") format("woff2"), url("../fonts/PlusJakartaSans.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.header {
  background: #344265;
}
.header__top, .header__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__top {
  padding-top: 16px;
}
@media (max-width: 1023.98px) {
  .header .header__top {
    flex-direction: column;
    gap: 10px;
  }
}
.header__search {
  background: #F8F8F8;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 100%;
  width: 282px;
  box-sizing: border-box;
  border-radius: 10px;
  padding: 9px 16px;
  border: 1px solid #344265;
  transition: all 0.3s ease;
}
.header__search:hover {
  border: 1px solid #FAFF00;
}
.header__search:hover .btn-search-icon path {
  fill: #777;
}
.header .btn-search-icon {
  background: transparent;
}
.header .input-search {
  width: 100%;
  background: transparent;
}
.header__tel {
  font-weight: 800;
  font-size: 1.125rem;
  line-height: 24px;
  color: #FFFFFF;
  transition: all 0.3s ease;
}
.header__tel:hover {
  color: #FAFF00;
}
.header .icon-tel path {
  fill: #FAFF00;
}
.header .icon-tel {
  margin-right: 7px;
}
.header__bottom {
  padding-top: 7px;
  align-items: start;
  position: relative;
}
@media (max-width: 1023.98px) {
  .header .header__bottom {
    padding-bottom: 7px;
  }
}
.header .menu {
  display: flex;
  align-items: center;
  height: 65px;
  gap: 30px;
}
.header .menu__link {
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
  letter-spacing: 0.01em;
  color: #FFFFFF;
  transition: all 0.3s ease;
}
.header .menu__link:hover {
  color: #5F719F;
}
.header .menu-toggle {
  position: relative;
  bottom: -23px;
  left: 0;
  width: 30px;
  height: 2px;
  background: #fff;
  line-height: 50px;
  font-size: 24px;
  cursor: pointer;
  display: none;
}
.header .menu-toggle::after, .header .menu-toggle::before {
  position: absolute;
  left: 0;
  content: "";
  width: 30px;
  height: 2px;
  background: #fff;
}
.header .menu-toggle::after {
  top: 8px;
}
.header .menu-toggle::before {
  top: -8px;
}
.header .open {
  width: 0;
  transition: all 0.3s ease;
}
.header .open::after,
.header .open::before {
  top: 0;
}
.header .open::before {
  transform: rotate(45deg);
}
.header .open::after {
  transform: rotate(-45deg);
}
@media (max-width: 1023.98px) {
  .header .menu-toggle {
    display: block;
  }
  .header .menu {
    position: absolute;
    width: 50%;
    height: calc(100vh - 50px);
    background: #333;
    top: 57px;
    left: -120%;
    transition: 0.5s;
  }
  .header .active {
    left: -20px;
    z-index: 99;
    overflow: hidden;
    padding-top: 20px;
  }
  .header .active .menu__item {
    padding: 10px;
  }
  .header .menu,
  .header .menu__item {
    display: block;
    text-align: center;
  }
  .header .menu__link {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  }
}
@media (max-width: 767.98px) {
  .header .menu {
    width: 80%;
  }
}
@media (max-width: 479.98px) {
  .header .menu {
    width: 100%;
  }
}
.header__button {
  background: #FAFF00;
  border-radius: 12px;
  padding: 9px 22px;
  display: flex;
  align-items: center;
  transition: all 0.3s ease-in-out;
}
.header__button:hover {
  background: #5F719F;
}
.header__button:hover .btn-setting-text {
  color: #F8F8F8;
}
.header__button:hover .btn-setting-icon path {
  fill: #F8F8F8;
}
.header__button .btn-setting-icon {
  margin-right: 9px;
}
.header__button .btn-setting-text {
  letter-spacing: -0.5px;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 16px;
  line-height: 26px;
  color: #344265;
}

.contant {
  margin: 48px 0 110px 0;
  max-width: 500px;
  position: relative;
  z-index: 90;
}
.contant__title {
  font-weight: 800;
  font-size: 40px;
  line-height: 48px;
  color: #344265;
  margin-bottom: 16px;
}
.contant__subtitle {
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: -0.6px;
  color: #485982;
  margin-bottom: 32px;
}
.contant__chek {
  margin-bottom: 40px;
}
.contant__chek .checklist__item {
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: -0.02em;
  margin-bottom: 20px;
  padding-left: 27px;
  color: #5F719F;
}
.contant__chek .checklist__svg {
  position: absolute;
  left: 0;
  top: 0;
}
.contant .btn-request {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 9px 22px;
  gap: 9px;
  background: #485982;
  border-radius: 12px;
  transition: all 0.3s ease;
}
.contant .btn-request__text {
  font-weight: 800;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: -0.5px;
  text-transform: uppercase;
  color: #FFFFFF;
}
.contant .btn-request:hover {
  background: #344265;
}

.contant-img {
  position: absolute;
  top: -48px;
  right: -146px;
  max-width: 610px;
}

.footer {
  background: #344265;
  padding-top: 56px;
  padding-bottom: 16px;
}
.footer__logo {
  margin-bottom: 15px;
}
.footer__content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 45px;
  color: #F8F8F8;
}
.footer__content .description {
  padding-top: 50px;
}
.footer__content .description .icon-tel path {
  fill: #FAFF00;
}
.footer__content .location {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  max-width: 200px;
  margin-top: 40px;
  margin-bottom: 32px;
  letter-spacing: -0.02em;
}
@media screen and (max-width: 767.98px) {
  .footer__content .location {
    max-width: 100%;
  }
}
.footer__content .footer-email {
  transition: all 0.3s ease;
}
.footer__content .footer-email:hover {
  color: #5F719F;
}
.footer__content .navigation {
  display: flex;
  justify-content: space-between;
  gap: 80px;
  margin-right: 35px;
}
.footer__content .navigation__title {
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  color: #FFFFFF;
  margin-bottom: 16px;
}
.footer__content .navigation__links {
  margin-bottom: 10px;
}
.footer__content .navigation__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  transition: all 0.3s ease;
  color: #F8F8F8;
}
.footer__content .navigation__link:hover {
  color: #5F719F;
}
@media screen and (max-width: 1023.98px) {
  .footer__content .navigation {
    margin-right: 0;
    gap: 40px;
  }
}
@media screen and (max-width: 767.98px) {
  .footer__content .navigation {
    display: none;
  }
}
.footer__links {
  display: flex;
  align-items: start;
  justify-content: space-between;
  margin-bottom: 75px;
}
.footer__links .socials {
  display: flex;
  gap: 20px;
}
.footer__links .socials__item {
  border-radius: 100%;
  background: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  transition: all 0.3s ease;
}
.footer__links .socials__item:hover {
  background: #5F719F;
}
@media screen and (max-width: 767.98px) {
  .footer .footer__links {
    flex-direction: column;
    gap: 15px;
  }
}
.footer__copyright {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #F8F8F8;
}

.container {
  max-width: 1110px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  font-family: "PlusJakartaSans", sans-serif;
}